









import Vue from 'vue'

import { ErrorMessages } from '../types'

export default Vue.extend({
  name: 'FormGeneratorError',
  props: {
    errors: {
      type: Array as () => ErrorMessages,
      default: () => []
    }
  },
  computed: {
    firstError (): string | null {
      return this.errors[0] || null
    }
  }
})
