





























import Vue from 'vue'

import { ErrorMessages } from '../types'

import FormGeneratorFormField from './FormGeneratorFormField.vue'
import FormGeneratorLabel from './FormGeneratorLabel.vue'
import FormGeneratorHelp from './FormGeneratorHelp.vue'
import FormGeneratorError from './FormGeneratorError.vue'

export default Vue.extend({
  name: 'FormGeneratorInput',
  components: {
    FormGeneratorFormField,
    FormGeneratorLabel,
    FormGeneratorHelp,
    FormGeneratorError
  },
  props: {
    id: {
      type: String,
      required: true
    },
    value: {
      type: [String, Number] as ((() => string) | (() => number))[],
      default: ''
    },
    errors: {
      type: Array as () => ErrorMessages,
      default: () => []
    },
    type: {
      type: String as () => 'text' | 'email' | 'number' | 'password',
      default: 'text',
      validator (value) {
        const allowedTypes = [
          'text',
          'email',
          'number',
          'password'
        ]

        return allowedTypes.includes(`${value}`)
      }
    },
    label: {
      type: String,
      default: ''
    },
    formGroupClassName: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    help: {
      type: String,
      default: ''
    },
    required: {
      type: Boolean,
      default: true
    },
    readonly: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    onInput (event: InputEvent) {
      const target = event.target as HTMLInputElement

      this.$emit('update:value', target.value)
      this.$emit('input', target.value)
    }
  }
})
